.radio-buttons-container {
  text-align: center;
}

.radio-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.radio-button {
  margin-right: 20px;
}

.radio-label {
  margin-left: 5px;
}


.app-profile-container {
  &__info,
  &__form {
    text-align: center;
    justify-content: center;
    width: 100%;
    align-items: center;
    display: grid;
    max-width: 500px;
    margin: 0 auto; // Center the container
  }

  &__info {
    padding-left: 1rem;
  }

  &__form {
    padding-left: 0.4rem;

    h4 {
      text-align: left;
    }

    &__group {
      margin-bottom: 15px;

      label {
        margin-bottom: 5px;
      }

      input {
        padding: 10px;
        width: 100%;
        box-sizing: border-box;
      }

      img {
        max-width: 100%;
        height: auto;
        margin-top: 10px;
      }
    }

    hr {
      margin: 20px 0;
    }

    button {
      padding: 10px;
      background-color: #007bff;
      color: #fff;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      transition: background-color 0.3s;

      &:hover {
        background-color: #0056b3;
      }
    }
  }
}

.app__profile-edit-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.app__profile-edit-container__form {
  width: 80%;
  max-width: 400px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;

  form {
    display: flex;
    flex-direction: column;

    &__group {
      margin-bottom: 15px;

      label {
        margin-bottom: 5px;
      }

      input {
        padding: 10px;
        width: 100%;
        box-sizing: border-box;
      }

      img {
        max-width: 100%;
        height: auto;
        margin-top: 10px;
      }
    }

    hr {
      margin: 20px 0;
    }

    button {
      padding: 10px;
      background-color: #007bff;
      color: #fff;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      transition: background-color 0.3s;

      &:hover {
        background-color: #0056b3;
      }
    }
  }
}
